.popup__input {
  min-height: 37px;
  outline: none;
  box-sizing: border-box;
  padding: 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-size: 14px;
  line-height: 17px;
  transition-property: border-bottom;
  transition-duration: 0.3s;
}

.popup__input::placeholder {
  color: #c4c4c4;
}
