.header {
  min-height: 120px;
  max-width: calc(100vw - 27px * 2);
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.header::before {
  position: absolute;
  z-index: -1;
  content: "";
  border-bottom: 1px solid #545454;
  box-sizing: border-box;
  top: 0;
  left: calc((100vw - 880px) / 2);
  width: 880px;
  max-width: 100vw;
  min-height: 120px;
}

@media screen and (max-width: 767px) {
  .header {
    min-height: 85px;
  }

  .header::before {
    min-height: 85px;
  }
}

@media screen and (max-width: 880px) {
  .header::before {
    left: 0;
  }
}
